h1{
  margin: 20px;
}
ul{
    margin: 10px;
}
form{
    margin: 5px;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.cardd{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-left: 10px;
  overflow: hidden;
}

.cardd p{
  padding: 5px;
  text-align: center;
}

.cardd img{
  max-width: 170px;
}